import {mapState, mapActions} from 'vuex'
export default {
  data () {
    return {
      form: {
        mobile: 13680065830,
        password: ''
      }
    }
  },
  computed: {
    ...mapState('common', ['identifyingCode'])
  },
  methods: {
    ...mapActions('common', ['getIdentifyingCode']),
    ...mapActions('user', ['login']),
    loginHandler () {
      this.login(this.form).then(res => {
        res && this.$go('/home')
      })
    }
  }
}



